import Vue from 'vue';
/**
 * @typedef {import('./state').cardState}
 */
import state from './state';
/** @type {import('axios').AxiosInstance} */
const http = Vue.$http;
export default {
  namespaced: true,
  state,
  mutations: {
    /**
     * update create card data
     * @param {state} state
     * @param {state['createCardData']} payload
     */
    updateCreateCardData(state, payload) {
      state.createCardData = payload;
    },
    /**
     * update selected card
     * @param {state} state
     * @param {state['createCardData']} payload
     */
    updateSelectedCard(state, payload) {
      state.selectedCard = payload;
    },
    /**
     * update selected card
     * @param {state} state
     * @param {state['cards']} payload
     */
    updateCards(state, payload) {
      state.cards = payload;
    },
    /**
     * update replace card data
     * @param {state} state
     * @param {state['replaceCardData']} payload
     */
    updateReplaceCardDate(state, payload) {
      state.replaceCardData = payload;
    },
    /**
     * update Sent Card
     * @param {state} state
     * @param {state['sentCards']} payload
     */
    updateSentCards(state, payload) {
      state.sentCards = payload;
    },
  },
  getters: {
    /**
     * get create card data
     * @param {state} state
     * @returns {state['createCardData']}
     */
    getCreateCardData(state) {
      return state.createCardData;
    },
    /**
     * get selected card data
     * @param {state} state
     * @returns {state['selectedCard']}
     */
    getSelectedCard(state) {
      return state.selectedCard;
    },
    /**
     * get cards
     * @param {state} state
     * @returns {state['cards']}
     */
    getCards(state) {
      return state.cards;
    },
    /**
     * get replace card data
     * @param {state} state
     * @returns {state['replaceCardData']}
     */
    getReplaceCardData(state) {
      return state.replaceCardData;
    },
    /**
     * get sent cards
     * @param {state} state
     * @returns {state['sendCards']}
     */
    getSentCards(state) {
      return state.sentCards;
    },
  },
  actions: {
    /**
     * create card
     * @param {Object} param0
     * @param {Function} param0.commit
     * @returns {Promise<import('./state').cardState['createCardData']>}
     */
    createCard({ commit }, payload) {
      return new Promise((resolve, reject) => {
        http
          .post('/v1/card', payload)
          .then(({ data }) => {
            console.log(data);
            commit('updateSelectedCard', data);
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * list all card
     * @param {*} param0
     * @param {string} accountId
     */
    listAllCardByAccount({ commit, state }, {accountId, limit = 25, offset = 0}) {
      return new Promise((resolve, reject) => {
        http
          .get(`/v1/card?accountId=${accountId}&limit=${limit}&offset=${offset}`)
          .then(({ data }) => {
            let d = {};
            if(offset === 0) {
              d = data;
            } else {
              const cards = {...state.cards};
              d = {
                data: [...cards.data,...data.data],
                total: data.total
              }
            }
            commit('updateCards', d);
            resolve(d);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * list sent cards
     * @param {*} param0
     * @param {string} accountId
     */
    listSentCardsByAccount({ commit, state }, {accountId, limit = 25, offset = 0}) {
      return new Promise((resolve, reject) => {
        http
          .get(`/v1/send/card?accountId=${accountId}&limit=${limit}&offset=${offset}`)
          .then(({ data }) => {
            let d = {};
            if(offset === 0) {
              d = data;
            } else {
              const cards = {...state.cards};
              d = {
                data: [...cards.data,...data.data],
                total: data.total
              }
            }
            commit('updateSentCards', d);
            resolve(d);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * Cancel sent cards
     * @param {*} param0
     * @param {string} cardId
     */
    cancelSentCard(_, cardId) {
      console.log(cardId)
      return new Promise((resolve, reject) => {
        http
          .post('/v1/send/card/cancel', {cardId})
          .then((data) => {
            resolve(data.data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * Activate card
     * @param {*} param0
     * @param {string} param1.cardId
     * @param {*} param1.payload
     */
    activate(_, { cardId, payload }) {
      return new Promise((resolve, reject) => {
        http
          .patch(`/v1/card/${cardId}/activate`, payload)
          .then(({ data }) => {
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * fetch uniqe token for vgs show
     * @param {*} _ 
     * @param {*} cardId 
     * @returns 
     */
    showToken(_, cardId) {
      return new Promise((resolve, reject) => {
        http
          .post(`v1/card/${cardId}/show-token`)
          .then(({ data }) => {
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * @deprecated
     * @param {*} _
     * @param {string} cardId
     */
    cardUnredact(_, cardId) {
      return new Promise((resolve, reject) => {
        http
          .get(`v1/card/${cardId}/unredact`)
          .then(({ data }) => {
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * Delete card
     * @param {*} _
     * @param {string} cardId
     */
    cardDelete(_, cardId) {
      return new Promise((resolve, reject) => {
        http
          .delete(`/v1/card/${cardId}`)
          .then(({ data }) => {
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * Replace card
     * @param {*} _
     * @param {string} cardId
     */
    replaceCard(_, cardId) {
      //TODO: Need to change
      return new Promise((resolve, reject) => {
        http
          .delete(`/v1/card/${cardId}`)
          .then(({ data }) => {
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * update card
     * @param {Object} param0
     * @param {Function} param0.commit
     * @param {Object} param1
     * @param {string} param1.cardId
     * @param {Object} param1
     */
    updateCard({ commit }, { cardId, payload }) {
      return new Promise((resolve, reject) => {
        http
          .patch(`v1/card/${cardId}`, payload)
          .then(({ data }) => {
            commit('updateSelectedCard', data);
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * get atm pin vgs token
     * @param {*} _ 
     * @param {*} cardId 
     * @returns 
     */
    pinToken(_, cardId) {
      return new Promise((resolve, reject) => {
        http
          .post(`v1/card/${cardId}/pintoken`)
          .then(({ data }) => {
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    /**
     * find near atm
     */
    findNearATM(_, {cardId, lat, long, limit = 25, page = 1}) {
      return new Promise((resolve, reject) => {
        const offset = limit * (page - 1);
        http
          .get(`v1/card/atm?cardId=${cardId}&limit=${limit}&offset=${offset}&latitude=${lat}&longitude=${long}&radius=5`)
          .then(({ data }) => {
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });

    }
  }
};
