    border-radius: 4px;
  }

  .btn-cancel {
    border: 1px solid var(--branding);
    color: var(--branding);

    &:hover {
      border-color: #b9b9b9;
      background-color: #fff;
    }
  }

  .el-button {
    height: 38px;
    
    + .el-button {
      margin-left: 24px;
      background-color: var(--branding);
